import React, { useEffect, useState, Suspense } from 'react';
import { useParams } from "react-router-dom"; //     slug
import { fetchPageData } from "../api"; //       WordPress API
const HomeTemplate = () => {
  const { slug } = useParams(); //  slug  URL 
  const [page, setPage] = useState(null);

  useEffect(() => {
    const getPageData = async () => {
      const data = await fetchPageData(slug);
      // console.log("Page data for slug:", slug, data); //    
      setPage(data);
    };
    getPageData();
  }, [slug]); //     ,  slug  


  let TemplateComponent;
  return (
    <div>
      <h2>Welcome to the Home Template</h2>
      <p>This is the home page template.</p>
    </div>
  );
};

export default HomeTemplate;
